.audio-uploader {
  max-width: 800px;
  margin: 0 auto;
  padding: 30px;
}

.audio-uploader h2 {
  text-align: center;
  color: #333;
  margin-bottom: 30px;
  font-size: 2rem;
}

/* Upload Zone */
.upload-zone {
  border: 3px dashed #ccc;
  border-radius: 12px;
  padding: 60px 20px;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
  background: #fafafa;
}

.upload-zone:hover {
  border-color: #007bff;
  background: #f0f8ff;
}

.upload-zone.dragging {
  border-color: #28a745;
  background: #e8f5e9;
  transform: scale(1.02);
}

.upload-icon {
  font-size: 4rem;
  margin-bottom: 15px;
}

.upload-text {
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 8px;
  font-weight: 500;
}

.upload-hint {
  font-size: 0.9rem;
  color: #666;
}

/* Divider */
.divider {
  text-align: center;
  margin: 30px 0;
  position: relative;
}

.divider::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  height: 1px;
  background: #ddd;
}

.divider span {
  background: white;
  padding: 0 15px;
  position: relative;
  color: #999;
  font-weight: 600;
}

/* Recording Controls */
.recording-controls {
  text-align: center;
  margin: 20px 0;
}

.btn {
  padding: 15px 30px;
  font-size: 1.1rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 600;
  display: inline-flex;
  align-items: center;
  gap: 10px;
}

.btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.btn-record {
  background: #dc3545;
  color: white;
}

.btn-record:hover:not(:disabled) {
  background: #c82333;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(220, 53, 69, 0.3);
}

.btn-stop {
  background: #6c757d;
  color: white;
}

.btn-stop:hover {
  background: #5a6268;
}

.btn-primary {
  background: #007bff;
  color: white;
}

.btn-primary:hover {
  background: #0056b3;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(0, 123, 255, 0.3);
}

.btn-secondary {
  background: #6c757d;
  color: white;
}

.btn-secondary:hover {
  background: #5a6268;
}

.btn-icon {
  font-size: 1.3rem;
}

/* Recording Active */
.recording-active {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
}

.recording-indicator {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 1.2rem;
  font-weight: 600;
  color: #dc3545;
}

.recording-dot {
  width: 12px;
  height: 12px;
  background: #dc3545;
  border-radius: 50%;
  animation: pulse 1.5s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.5;
    transform: scale(1.2);
  }
}

/* Audio Preview */
.audio-preview {
  margin-top: 30px;
  padding: 25px;
  background: #f8f9fa;
  border-radius: 12px;
  border: 1px solid #dee2e6;
}

.audio-preview h3 {
  margin-top: 0;
  margin-bottom: 20px;
  color: #333;
  font-size: 1.3rem;
}

.file-info {
  margin-bottom: 15px;
}

.file-name {
  font-size: 1.1rem;
  font-weight: 600;
  color: #333;
  margin-bottom: 5px;
}

.file-size {
  font-size: 0.9rem;
  color: #666;
}

.audio-player {
  width: 100%;
  margin: 20px 0;
  border-radius: 8px;
}

.preview-actions {
  display: flex;
  gap: 15px;
  justify-content: center;
  margin-top: 20px;
}

/* Responsive */
@media (max-width: 768px) {
  .audio-uploader {
    padding: 20px 15px;
  }

  .audio-uploader h2 {
    font-size: 1.5rem;
  }

  .upload-zone {
    padding: 40px 15px;
  }

  .upload-icon {
    font-size: 3rem;
  }

  .preview-actions {
    flex-direction: column;
  }

  .btn {
    width: 100%;
    justify-content: center;
  }
}
